<template>
  <li class="card" @click="CardClick(item.id)">
    <div class="thumb">
      <img :src="item.picUrl" alt />
      <span>
        <i class="fa fa-headphones"></i>
        {{ translatePlayCount(item.playCount) }}
      </span>
    </div>
    <div class="name">{{ item.name }}</div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object
  },
  methods: {
    translatePlayCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      }
    },
    CardClick(param) {
      // window.console.log(this.$router,param)
      this.$router.push({ path: "playlist", query: { id: param } });
    }
  }
};
</script>

<style lang="less">
.thumb {
  position: relative;
  img {
    vertical-align: middle;
  }
  span {
    position: absolute;
    top: 5px;
    right: 5px;
    color: white;
    font-size: 10px;
  }
}
.name {
  padding: 3px 2px 0 ;
  font-size: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin: 0 5px 5px 5px;
  color: #333
}
</style>
