<template>
  <div class="hot">
    <div class="hot-music">
      <div class="hot-music-top"></div>
    </div>
    <!-- rgba(153,38,103,0.6) -->
    <SongListItem
      v-for="(item, index) in topList"
      :key="index"
      :song-item="item"
      :index="index"
      :isWatch="true"
      :topList="topList"
      @translate-currentid="$emit('translate-currentid',$event)"
    />
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  data() {
    return {
      topList: []
    };
  },
  components: {
    SongListItem
  },
  methods: {
    //热歌榜
    getTopList() {
      //http://api.kele8.cn/agent/
      this.axios.get("https://music.kele8.cn/top/list?idx=1").then(response => {
        this.topList = response.data.playlist.tracks.slice(0, 100);
      });
    }
  },
  created() {
    this.getTopList();
  }
};
</script>

<style lang="less">
.hot {
  .hot-music {
    position: relative;
    padding: 10% 13%;
    background: url("../assets/hot_music_bg_2x.jpg") no-repeat;
    background-size: cover;
    .hot-music-top {
      width: 142px;
      height: 62px;
      background: url("../assets/logo2.png") no-repeat;
      background-size: 166px 84px;
      background-position: -24px -25px;
    }
    &:after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 0;
      background: rgba(133, 0, 75, 0.2);
    }
  }
}
</style>