<template>
  <div class="player" :class="{paused:isPaused}" v-if="currentSongDetail">
    <audio ref="audio" :src="currentSongUrl" autoplay></audio>
    <div class="player-bar" @click.stop="isShowFullScreen = true;">
      <img :src="currentSongDetail.songs[0].al.picUrl" alt />
      <div class="singer">
        <h4>{{currentSongDetail.songs[0].name}}</h4>
        <p>{{currentSongDetail.songs[0].ar[0].name}}</p>
      </div>
      <template v-if="isP">
        <span class="fa fa-play-circle progress" @click.stop="pausedAudio"></span>
      </template>
      <template v-else>
        <span class="fa fa-pause-circle progress" @click.stop="pausedAudio"></span>
      </template>

      <span class="fa fa-list-ul list" @click.stop="songsList"></span>
    </div>

    <template v-if="isShowFullScreen && currentSongDetail">
      <div class="fullscreen">
        <div
          class="fullscreen-bg"
          :style="{'background-image':`url(${currentSongDetail.songs[0].al.picUrl})`}"
        ></div>
        <div class="shade"></div>
        <div class="element">
          <span class="fa fa-long-arrow-left go-back" @click="isShowFullScreen = false"></span>
          <template v-if="isNone">
            <ul class="lyric" @click="isNone = false">
              <li v-for="(item, index) in songsLyric" :key="index">{{item.text}}</li>
            </ul>
          </template>
          <div class="top" :class="{none:isNone,none2:!isNone}" @click="isNone=!isNone">
            <div class="needle">
              <img src="../assets/needle.png" alt :class="{active:!isP,going:isP}" />
            </div>
            <div class="plate">
              <div class="box">
                <img :src="currentSongDetail.songs[0].al.picUrl" alt :class="{active:isAc}" />
              </div>
            </div>
            <div class="songmessage">
              <h4>{{currentSongDetail.songs[0].name}}</h4>
              <p>{{currentSongDetail.songs[0].ar[0].name}}</p>
            </div>
          </div>
          <div class="bottom">
            <div class="progressbar">
              <div class="slide">
                <van-slider v-model="value" @change="onChange" class="progress" button-size="10px" />
              </div>
              <div class="playtime">
                <span>{{startTime}}</span>
                <span>{{DealTime(currentSongDetail.songs[0].dt/1000)}}</span>
              </div>
            </div>
            <div class="type">
              <span @click="playPatternClick">
                <template v-if="circulationPlay">
                  <!-- 顺序播放 -->
                  <i class="fa fa-share"></i>
                </template>
                <template v-if="singlePlay">
                  <!-- 单曲循环 -->
                  <i class="fa fa-refresh"></i>
                </template>
                <template v-if="randomPlay">
                  <!-- 随机播放 -->
                  <i class="fa fa-random"></i>
                </template>
              </span>
              <span>
                <i class="fa fa-step-backward" @click="previous()"></i>
              </span>
              <span @click="pausedAudio">
                <template v-if="isP">
                  <i class="fa fa-play-circle"></i>
                </template>
                <template v-else>
                  <i class="fa fa-pause-circle"></i>
                </template>
              </span>
              <span>
                <i class="fa fa-step-forward" @click="next()"></i>
              </span>
              <span @click="kong">
                <i class="fa fa-list-ul"></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </template>
    <div class="sliding">
      <div class="songslist-bg" v-if="songslistBg" @click="isSongs=false;songslistBg=false"></div>
      <ul class="songslist" :class="{active:isSongs}">
        <li
          v-for="(item, index) in musicList"
          :key="index"
          @click="tracking(item.id)"
          :class="{every:isevery}"
        >
          <p class="plis">
            {{item.name}}
            <template v-if="item.song">
              <span>{{item.song.artists[0].name}}</span>
            </template>
            <template v-if="item.ar">
              <span>{{item.ar[0].name}}</span>
            </template>
            <template v-if="item.artists">
              <span>{{item.artists[0].name}}</span>
            </template>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentSongDetail: null,
      isPaused: false,
      isShowFullScreen: this.showFullscreen || false,
      currentTime: 0,
      isP: false,
      value: 0,
      startTime: "00:00",
      endtime: "00:00",
      circulationPlay: true,
      singlePlay: false,
      randomPlay: false,
      PlayPatternCurrent: 0,
      isAc: false,
      musicIndex: null,
      musicList: [],
      musicId: null,
      songs: false,
      isSongs: false,
      songslistBg: false,
      isevery: true,
      isNone: false,
      songsLyric: Object
    };
  },
  props: ["currentSongId", "showFullscreen"],

  computed: {
    currentSongUrl: {
      get: function() {
        return (
          "https://music.163.com/song/media/outer/url?id=" +
          this.musicId +
          ".mp3"
        );
      },
      set: function(newValue) {
        return (this.musicId = newValue);
      }
    }
  },
  methods: {
    kong() {
      window.console.log("xxxxxxxxxxxxxxxxxxxxx");
      this.songs = true;
      this.isSongs = true;
      this.songslistBg = true;
    },
    songsList() {
      this.songs = true;
      this.isSongs = true;
      this.songslistBg = true;
    },
    previous() {
      this.isP = false;
      if (this.PlayPatternCurrent == 2) {
        this.random();
      } else {
        if (this.musicIndex) {
          this.againGetSongDetail(this.musicList[--this.musicIndex].id);
          this.currentSongUrl = this.musicList[this.musicIndex].id;
        } else if (this.musicIndex == 0) {
          (this.musicIndex = this.musicList.length - 1),
            (this.currentSongUrl = this.musicList[this.musicIndex].id);
          this.againGetSongDetail(this.musicList[this.musicIndex].id);
        }
      }
    },
    next() {
      this.isP = false;
      if (this.PlayPatternCurrent == 2) {
        this.random();
      } else {
        if (this.musicIndex >= 0 && this.musicIndex < this.musicList.length) {
          this.musicIndex++;
          this.againGetSongDetail(this.musicList[this.musicIndex].id);
          this.currentSongUrl = this.musicList[this.musicIndex].id;
        }
        if (this.musicIndex > this.musicList.length - 1) {
          this.musicIndex = 0;
          this.againGetSongDetail(this.musicList[this.musicIndex].id);
          this.currentSongUrl = this.musicList[this.musicIndex].id;
        }
      }
      //下一曲有问题待修改
    },
    random() {
      let x = Math.floor(Math.random() * this.musicList.length - 1);
      this.musicIndex = x;
      this.againGetSongDetail(this.musicList[this.musicIndex].id);
      this.currentSongUrl = this.musicList[this.musicIndex].id;
      window.console.log(x);
    },
    single() {
      window.console.log(this.musicId);
    },
    onChange(value) {
      let au = this.$refs.audio;
      au.currentTime = (value / 100) * au.duration;
      this.$el.querySelector(".van-slider__bar").style.width = value + "%";
    },
    adc() {
      let au = this.$refs.audio;
      au.onerror = function() {
        window.console.log("yyyyyyyyyyyyyyyyyy");
        this.next();
      };
      var Id = setInterval(() => {
        this.startTime = this.DealTime(au.currentTime);
        if (this.PlayPatternCurrent == 0) {
          if (au.currentTime == au.duration) {
            this.next();
          }
        }
        if (this.PlayPatternCurrent == 1) {
          if (au.currentTime == au.duration) {
            window.console.log(this.musicIndex);
            // this.single();
          }
        }
        if (this.PlayPatternCurrent == 2) {
          if (au.currentTime == au.duration) {
            this.random();
          }
        }
        if (this.isP) {
          window.clearInterval(Id);
        }
        // window.console.log(this.startTime, au.duration);
      }, 800);
      au.ontimeupdate = () => {
        if (this.$el.querySelector(".van-slider__bar")) {
          this.$el.querySelector(".van-slider__bar").style.width =
            (au.currentTime / au.duration) * 100 + "%";
        }
      };
    },
    //单曲，循环，随机
    playPatternClick() {
      this.PlayPatternCurrent++;
      if (this.PlayPatternCurrent >= 3) {
        this.PlayPatternCurrent = 0;
      }
      if (this.PlayPatternCurrent <= 3) {
        if (this.PlayPatternCurrent == 0) {
          this.circulationPlay = true;
          this.singlePlay = false;
          this.randomPlay = false;
        } else if (this.PlayPatternCurrent == 1) {
          this.circulationPlay = false;
          this.singlePlay = true;
          this.randomPlay = false;
        } else {
          this.circulationPlay = false;
          this.singlePlay = false;
          this.randomPlay = true;
        }
      }
      window.console.log("xxx");
    },
    //时间处理方法
    DealTime(time) {
      time = Math.floor(time);
      let differminute = parseInt(time / 60);
      let differseconds = time % 60;
      if (differminute < 10) {
        differminute = "0" + differminute;
      }
      if (differseconds < 10) {
        differseconds = "0" + differseconds;
      }
      let t = differminute + ":" + differseconds;
      return t;
    },
    //获取歌曲详情
    getSongDetail() {
      this.axios
        .get(
          // "http://api.kele8.cn/agent/https://musicapi.leanapp.cn/song/detail",
          "https://music.kele8.cn/song/detail",

          {
            params: {
              ids: this.currentSongId
            }
          }
        )
        .then(response => {
          ({ data: this.currentSongDetail } = response);
          this.musicId = this.currentSongId;
          this.getLyric();
          this.musicIndex = this.$store.state.currentIndex;
          if (window.location.hash.match("recommend")) {
            this.musicList = this.$store.state.newsongList;
          } else if (window.location.hash.match("hot")) {
            this.musicList = this.$store.state.hotList;
          } else if (window.location.hash.match("search")) {
            this.musicList = this.$store.state.searchSongs;
          } else if (window.location.hash.match("playlist")) {
            this.musicList = this.$store.state.tracks;
          } else if (window.location.hash.match("artist")) {
            this.musicList = this.$store.state.songs;
          }

          // this.musicList = this.$store.state.songArray.hotArray
        });
    },
    againGetSongDetail(n) {
      this.axios
        .get("https://music.kele8.cn/song/detail?ids=" + n)
        .then(response => {
          ({ data: this.currentSongDetail } = response);
        });
    },
    //拿歌词
    getLyric() {
      this.axios
        .get("https://music.kele8.cn/lyric?id=" + this.musicId)
        .then(response => {
          this.lyric = response.data.lrc.lyric.split("\n");
          window.console.log(this.lyric);
          this.songsLyric = this.jiexi(this.lyric);
          window.console.log(this.songsLyric);
        });
    },
    jiexi(ly) {
      let arr = [];
      ly.forEach((element, index) => {
        // window.console.log(Number(element.slice(1, 9).slice(0,2))*60 + Number(element.slice(1, 9).slice(3)))
        let t = element.slice(1, 9);
        arr[index] = {
          time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
          text: element.slice(11).trim()
        };
      });
      // arr.pop();
      return arr;
    },
    pausedAudio() {
      let audio = this.$el.querySelector("audio");
      //刚开始是this.isPause为false,判断后audio.paused为true,再赋值给this.isPause,所以点击完this.isPause为true
      if (this.isPaused) {
        this.isP = false;
        audio.play();
        this.adc();
      } else {
        this.isP = true;
        audio.pause();
      }
      this.isPaused = audio.paused;
      this.isAc = !this.isAc;
      // window.console.log(audio.duration)
    },
    tracking(n) {
      window.console.log(n);
      this.againGetSongDetail(n);
      this.currentSongUrl = n;
      this.isP = false;
    }
  },
  created() {
    this.getSongDetail();
  },
  watch: {
    currentSongId: function() {
      this.getSongDetail();
      this.isShowFullScreen = this.showFullscreen;
      this.isP = false;
    },
    currentSongDetail() {
      //
      this.$nextTick(() => {
        this.adc();
      });
    }
  }
};
</script>

<style lang="less">
.player {
  &.paused {
    .player-bar {
      img {
        animation-play-state: paused;
      }
    }
  }
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  audio {
    height: 60px;
  }
  div.player-bar {
    box-shadow: 0 3px 10px 0 #000000;
    display: flex;
    justify-content: center;
    background: #85004b;
    align-items: center;
    padding: 5px 8px;
    img {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      animation: myrotate 5s linear infinite;
    }
    .singer {
      flex: 1;
      color: white;
      margin-left: 10px;
      padding: 0 5px 0 5px;
      h4 {
        margin-bottom: 3px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      p {
        font-size: 10px;
        color: #999;
      }
    }
    .progress,
    .list {
      width: 33px;
      text-align: center;
      font-size: 34px;
      color: gainsboro;
    }
    .progress {
      // background: yellow;
      height: 33px;
      position: relative;
      left: -5px;
    }
    .list {
      font-size: 20px;
      margin-left: 3px;
    }
  }
  div.fullscreen {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    transform: scale(1.2);
    div.fullscreen-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      z-index: 2;
      filter: blur(10px);
    }
    div.shade {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      z-index: 3;
    }
    div.element {
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 4;
      overflow: hidden;
      margin-top: 8%;
      span.go-back {
        position: fixed;
        left: 0;
        font-size: 24px;
        color: white;
        margin-left: 12%;
        margin-top: 10%;
        padding: 6px;
      }
      ul.lyric {
        width: 80%;
        width: 80%;
        height: 387px;
        margin: 20% auto;
        padding: 10px 20px;
        overflow-y: scroll;
        text-align: center;
        color: #eee;
        li{
          line-height: 24px;
          font-size: 14px;
        }
      }
      div.top {
        &.none {
          opacity: 0;
          transition: all 0.3s;
        }
        &.none2 {
          opacity: 1;
          transition: all 0.3s;
        }
        div.needle {
          position: absolute;
          top: 9%;
          left: 43%;
          height: 16vh;
          img {
            height: 100%;
            transform: rotate(-55deg);
            transform-origin: top right;
            &.active {
              height: 100%;
              transition: all 0.5s;
              transform-origin: top right;
              transform: rotate(-30deg);
            }
            &.going {
              height: 100%;
              transition: all 0.5s;
              transform-origin: top right;
              transform: rotate(-55deg);
            }
          }
        }
        div.plate {
          width: 100%;
          margin-top: 20vh;
          div.box {
            margin: 0 auto;
            width: 60%;
            height: 34vh;
            background-image: url("../assets/disc.d3bdd108.png");
            background-size: 100%;
            background-repeat: no-repeat;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
              width: 60%;
              height: 60%;
              border-radius: 50%;
              animation: myrotate 5s linear infinite;
              &.active {
                animation-play-state: paused;
              }
            }
          }
        }
        div.songmessage {
          margin: 10px 60px;
          color: white;
          h4 {
            margin-bottom: 3px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
          p {
            font-size: 10px;
            color: #999;
          }
        }
      }

      div.bottom {
        width: 100%;
        position: fixed;
        left: 0;
        bottom: 10%;
        div.progressbar {
          div.slide {
            width: 72%;
            margin: 60px auto 0;
            .progress {
              width: 100%;
              .van-slider__bar {
                background-color: #d0006e;
                .van-slider__button {
                  width: 10px;
                  height: 10px;
                }
              }
            }
          }
          div.playtime {
            width: 72%;
            margin: 4px auto;
            display: flex;
            justify-content: space-between;
            font-size: 11px;
            color: rgba(255, 255, 255, 0.5);
            &:nth-child(1) {
            }
            &:nth-child(2) {
            }
          }
        }
        div.type {
          width: 75%;
          margin: 10px auto;
          display: flex;
          text-align: center;
          font-size: 22px;
          color: white;
          font-weight: initial;
          span {
            &:nth-child(3) {
              font-size: 30px;
              margin-top: -8px;
            }
            flex: 1;
          }
        }
      }
    }
  }
  div.sliding {
    div.songslist-bg {
      width: 100%;
      height: 100vh;
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 0;
    }
    ul.songslist {
      // margin-top: 20vh;
      position: absolute;
      bottom: -520px;
      left: 0;
      background-color: #fff;
      width: 100%;
      height: 300px;
      padding: 8px 12px;
      overflow-y: scroll;
      border-radius: 20px 20px 0 0;
      &.active {
        bottom: 0;
        transition: all 0.3s;
      }
      li {
        &.every {
          color: #85004b;
        }
        margin-top: 15px;
        p.plis {
          &.active {
            color: #d0006e;
          }
          span {
            color: #888;
            font-size: 12px;
          }
        }
      }
    }
  }

  @keyframes myrotate {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
}
</style>