import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        currentIndex: null,
        newsongList: [],
        hotList:[],
        searchSongs:[],
        tracks:[],
        songs:[]
    },
    mutations: {
        submitcurrentIndex(state, i) {
            state.currentIndex = i;
        },
        submitNewsongs(state, i) {
            state.newsongList = i
        },
        submitHotlist(state,i){
          state.hotList = i
        },
        submitSearchsongs(state,i){
          state.searchSongs = i
        },
        submitTracks(state,i){
          state.tracks = i
        },
        submitSongs(state,i){
          state.songs = i
        },
    },
    actions: {},
    modules: {}
});
