<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="list">
      <SongListCard v-for="(item, index) in personalized" :key="index" :item="item" />
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <SongListItem
      v-for="(item, index) in newsong"
      :key="index"
      :song-item="item"
      :isWatch="false"
      :newsong="newsong"
      :index="index"
      @translate-currentid="$emit('translate-currentid',$event)"
    />
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import SongListCard from "@/components/SongListCard.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  components: {
    HomeTitle,
    SongListCard,
    SongListItem
  },
  data() {
    return {
      personalized: [],
      newsong: []
      // isShowQuality:true
    };
  },
  methods: {
    getPersonalized() {
      let personalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      if (personalized && Date.now() < personalized.time) {
        //本地拿数据
        this.personalized = personalized.data;
      } else {
        //推荐歌单
        this.axios
          // .get("http://api.kele8.cn/agent/http://musicapi.leanapp.cn/personalized?limit=6")
          .get(" https://music.kele8.cn/personalized?limit=6")

          .then(response => {
            // this.personalized = response.data.result;
            ({ result: this.personalized } = response.data);
            //本地存储  
            window.localStorage.setItem(
              "personalized",
              JSON.stringify({
                time: Date.now() + 120 * 60 * 1000,
                data: this.personalized
              })
            );
          });
      }
    },
    getNewsong() {
      let newsong = JSON.parse(window.localStorage.getItem("newsong"));
      if (newsong && Date.now() < newsong.time) {
        this.newsong = newsong.data;
      } else {
        //最新音乐
        this.axios
          .get("https://music.kele8.cn/personalized/newsong")
          .then(response => {
            ({ result: this.newsong } = response.data);
            window.localStorage.setItem(
              "newsong",
              JSON.stringify({
                time: Date.now() + 120 * 60 * 1000,
                data: this.newsong
              })
            );
          });
      }
    }
  },

  created() {
    this.getPersonalized();
    this.getNewsong();
  }
};
</script>

<style lang="less">
ul.list {
  display: flex;
  flex-wrap: wrap;
  li.card {
    flex: 1 0 30%;
    &:nth-child(3n + 2) {
      margin: 0 5px;
    }
  }
}
</style>
