<template>
  <div class="search">
    <div class="input">
      <label for>
        <input
          type="text"
          class="search-input"
          v-model.trim="inputValue"
          placeholder="搜索歌曲、歌手、专辑"
          @keyup.enter="getSearchMultimatch(inputValue);getSearchSongs(inputValue)"
          @input="vary"
        />
        <span class="close" v-if="inputValue" @click="del"></span>
      </label>
    </div>
    <template v-if="hotExit">
      <div class="hot">
        <h4>热门搜索</h4>
        <ul class="keywords">
          <li
            v-for="(item, index) in hotSearchKeyWords"
            :key="index"
            @click="hotSearch(item.first)"
          >{{item.first}}</li>
        </ul>
        <div class="history"></div>
      </div>
    </template>
    <template v-if="inputValue && !searchMultimatch &&  ffff">
      <div class="recom">
        <h3>搜索"{{inputValue}}"</h3>
        <ul class="lis">
          <li
            v-for="(item,index) in searchSuggests"
            :key="index"
            @click="searching(item.keyword)"
          >{{item.keyword}}</li>
        </ul>
      </div>
    </template>

    <div class="multimatch" v-if="searchMultimatch">
      <h4>最佳匹配</h4>
      <template v-if="searchMultimatch.artist">
        <div
          class="artist"
          v-for="(item) in searchMultimatch.artist"
          :key="item.id"
          @click="jumpArtist"
        >
          <img :src="item.picUrl" alt />
          <h5>歌手: {{item.name}}</h5>
          <span>></span>
        </div>
      </template>
      <template v-if="searchMultimatch.mv">
        <div class="mv" v-for="(item) in searchMultimatch.mv" :key="item.id"  @click="jumpArtistMv">
          <img :src="item.cover" alt />
          <h5>
            <p>
              MV:
              <span>{{item.name}}</span>
            </p>
            <p>{{item.artistName}}</p>
          </h5>
          <span>></span>
        </div>
      </template>
    </div>
    <ul class="searchSong" v-if="searchSongs">
      <SongListItem
        v-for="(item, index) in searchSongs"
        :key="item.id"
        :song-item="item"
        :index="index"
        :search-songs="searchSongs"
        @translate-currentid="$emit('translate-currentid', $event)"
      ></SongListItem>
    </ul>
    <div v-if="loading">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        style="margin:auto;background:#fff;display:block;"
        width="50px"
        height="50px"
        viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid"
      >
        <g transform="rotate(180 50 50)">
          <rect x="15" y="15" width="10" height="40.2755" fill="#d0006e">
            <animate
              attributeName="height"
              values="50;70;30;50"
              keyTimes="0;0.33;0.66;1"
              dur="1s"
              repeatCount="indefinite"
              calcMode="spline"
              keySplines="0.5 0 0.5 1;0.5 0 0.5 1;0.5 0 0.5 1"
              begin="-0.4s"
            />
          </rect>
          <rect x="35" y="15" width="10" height="30.4046" fill="#d0006e">
            <animate
              attributeName="height"
              values="50;70;30;50"
              keyTimes="0;0.33;0.66;1"
              dur="1s"
              repeatCount="indefinite"
              calcMode="spline"
              keySplines="0.5 0 0.5 1;0.5 0 0.5 1;0.5 0 0.5 1"
              begin="-0.2s"
            />
          </rect>
          <rect x="55" y="15" width="10" height="50.2804" fill="#d0006e">
            <animate
              attributeName="height"
              values="50;70;30;50"
              keyTimes="0;0.33;0.66;1"
              dur="1s"
              repeatCount="indefinite"
              calcMode="spline"
              keySplines="0.5 0 0.5 1;0.5 0 0.5 1;0.5 0 0.5 1"
              begin="-0.6s"
            />
          </rect>
          <rect x="75" y="15" width="10" height="63.0947" fill="#d0006e">
            <animate
              attributeName="height"
              values="50;70;30;50"
              keyTimes="0;0.33;0.66;1"
              dur="1s"
              repeatCount="indefinite"
              calcMode="spline"
              keySplines="0.5 0 0.5 1;0.5 0 0.5 1;0.5 0 0.5 1"
              begin="-1s"
            />
          </rect>
        </g>
      </svg>
    </div>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  data() {
    return {
      inputValue: null,
      hotExit: true,
      hotSearchKeyWords: [],
      searchSuggests: [],
      loading: false,
      searchMultimatch: null,
      searchSongs: [],
      ffff: false
    };
  },
  components: {
    SongListItem
  },
  methods: {
    vary() {
      this.searchMultimatch = null;
      this.ffff = true;
      this.searchSongs = true;
    },
    del() {
      this.inputValue = "";
      this.searchSongs = false;
    },
    hotSearch(n) {
      let name = n;
      this.getSearchMultimatch(name);
      this.getSearchSongs(name);
      this.inputValue = name;
      this.hotExit = false;
    },
    searching(n){
      let name = n;
      this.getSearchMultimatch(name),
      this.getSearchSongs(name);
    },
    //热词搜索
    getHotSearch() {
      this.axios.get("https://music.kele8.cn/search/hot").then(response => {
        ({ hots: this.hotSearchKeyWords } = response.data.result);
      });
    },

    //搜索建议，返回单曲，歌手，歌单 ,mv 信息
    getSearchSuggest() {
      this.loading = true;
      this.axios
        .get("https://music.kele8.cn/search/suggest", {
          params: {
            keywords: this.inputValue,
            type: "mobile"
          }
        })
        .then(response => {
          ({ allMatch: this.searchSuggests } = response.data.result);
          // window.console.log(response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    //拿mv
    getSearchMultimatch(n) {
      this.loading = true;
      this.axios
        .get("https://music.kele8.cn/search/multimatch", {
          params: {
            keywords: n
          }
        })
        .then(response => {
          ({ result: this.searchMultimatch } = response.data);
          // window.console.log(response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    //拿歌
    getSearchSongs(n) {
      this.loading = true;
      this.axios
        .get("https://music.kele8.cn/search", {
          params: {
            keywords: n,
            limit: 8,
            offset: 0
          }
        })
        .then(response => {
          ({ songs: this.searchSongs } = response.data.result);
          // window.console.log(response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    jumpArtist() {
      this.$router.push({
        path: "/artist",
        query: {
          id: this.searchMultimatch.artist[0].id,
          name:this.searchMultimatch.artist[0].name,
          img:this.searchMultimatch.artist[0].picUrl
        }
      });
    },
    jumpArtistMv() {
      this.$router.push({
        path: "/artistMv",
        query: {
          id: this.searchMultimatch.artist[0].id,
        }
      });
    }
  },
  watch: {
    inputValue() {
      if (this.inputValue) {
        this.getSearchSuggest(this.inputValue);
        this.hotExit = false;
      } else {
        (this.hotExit = true), (this.searchMultimatch = false);
      }
    }
  },
  created() {
    this.getHotSearch();
  }
};
</script>

<style lang='less'>
.search {
  background: #fcfcfd;
  div.input {
    width: 100%;
    border-bottom: 1px solid #eee;
    label {
      position: relative;
      left: 5%;
      &:before {
        content: "";
        position: absolute;
        top: 6px;
        left: 8px;
        width: 14px;
        height: 14px;
        // background: red;
        background-image: url("data:image/svg+xml;base64,");
      }
      input.search-input {
        margin-top: 12px;
        margin-bottom: 12px;
        width: 90%;
        height: 30px;
        border: 1px solid #ebecec;
        outline: none;
        border-radius: 30px;
        background: #eee;
        padding: 2px 26px;
        font-size: 12px;
        font-weight: normal;
      }
      span.close {
        position: absolute;
        top: 6px;
        right: 8px;
        width: 14px;
        height: 14px;
        background: url("data:image/svg+xml;base64,");
      }
    }
  }
  .hot {
    padding: 0 12px;
    h4 {
      font-size: 12px;
      line-height: 12px;
      margin: 10px 0;
      color: #666;
    }
    ul.keywords {
      display: flex;
      flex-wrap: wrap;
      li {
        color: rgba(0, 0, 0, 0.8);
        line-height: 25px;
        padding: 0 14px;
        margin-right: 8px;
        margin-bottom: 8px;
        border-radius: 15px;
        border: 1px solid #999;
      }
    }
  }
  div.recom {
    padding: 0 10px;
    h3 {
      padding: 0 10px 0 0;
      color: #d0006e;
      height: 40px;
      line-height: 40px;
      font-size: 15px;
      border-bottom: 1px solid #eee;
    }
    ul.lis {
      li {
        width: 100%;
        height: 40px;
        padding-left: 22px;
        position: relative;
        line-height: 40px;
        font-size: 15px;
        color: #333;
        border-bottom: 1px solid #eee;
        &::before {
          content: "";
          position: absolute;
          top: 12px;
          left: 0;
          width: 16px;
          height: 16px;
          background-image: url("data:image/svg+xml;base64,");
        }
      }
    }
  }
  div.multimatch {
    padding: 0 10px;
    h4 {
      font-size: 12px;
      line-height: 24px;
      color: #666;
    }
    div.artist {
      display: flex;
      align-items: center;
      padding: 5px 8px 5px 0;
      border-bottom: 1px solid #eee;
      box-sizing: border-box;
      img {
        width: 66px;
      }
      h5 {
        margin-left: 6%;
        font-size: 17px;
        line-height: 30px;
        color: #333;
        flex: 1;
      }
      span {
        color: #888;
        font-size: 20px;
      }
    }
    div.mv {
      position: relative;
      padding: 6px 8px 5px 0;
      display: flex;
      line-height: 50px;
      align-items: center;
      border-bottom: 1px solid #eee;
      &::after {
        content: "";
        width: 20px;
        height: 20px;
        position: absolute;
        top: 33%;
        left: 12%;
        background: url("../assets/down.svg") 0 0 / contain no-repeat;
      }
      img {
        width: 33%;
      }
      h5 {
        flex: 1;
        margin-left: 5%;
        p {
          &:first-child {
            font-size: 16px;
            line-height: 24px;
            span {
              font-size: 18px;
              color: #507daf;
            }
          }
          &:last-child {
            line-height: 16px;
            color: #888;
            font-size: 12px;
          }
        }
      }
      span {
        color: #888;
        font-size: 20px;
      }
    }
  }
  ul.searchSong {
    li {
      border-bottom: 1px solid #eee;
    }
  }
}
</style>