<template>
  <!-- 判断isActive来执行active类名 -->
  <li
    class="song"
    @click="setCurrentSongId(songItem.id);setCurrentArray(index);"
    :class="{active:isActive}"
  >
    <!-- 如果有ar,才有数字；如果iswatch是true，才有thunder函数（数字拼接） -->
    <div class="left" v-if="songItem.ar">
      <div v-if="isWatch" class="kkkk">{{thunder()}}</div>
      <div v-else>{{index + 1}}</div>
    </div>

    <dl class="center">
      <dt>{{ songItem.name }}</dt>

      <dd v-if="songItem.song">
        <i class="sq"></i>
        {{songItem.song.artists[0].name}}-{{songItem.song.album.name}}
      </dd>
      <dd v-else-if="songItem.h">
        <i class="sq" v-if="songItem.h && isWatch"></i>
        {{songItem.ar[0].name}}-{{songItem.al.name}}
      </dd>
      <dd v-if="songItem.artists">
        <i class="sq"></i>
        {{songItem.artists[0].name}}-{{songItem.album.name}}
      </dd>
    </dl>
    <div class="right">
      <i class="play"></i>
    </div>
  </li>
</template>

<script>
export default {
  data() {
    return {
      isActive: true
    };
  },
  props: [
    "song-item",
    "index",
    "isWatch",
    "newsong",
    "tracks",
    "topList",
    "searchSongs",
    "songs"
  ],
  methods: {
    setCurrentSongId(n) {
      this.$emit("translate-currentid", {
        songId: n,
        showFullscreen: window.location.hash.match("search") ? true : false
      });
    },
    setCurrentArray(n) {
      this.$store.commit("submitcurrentIndex", n);
      this.$store.commit("submitNewsongs", this.newsong);
      this.$store.commit("submitHotlist", this.topList);
      this.$store.commit("submitSearchsongs",this.searchSongs)
      this.$store.commit("submitTracks",this.tracks)
      this.$store.commit("submitSongs",this.songs)
      
    },
    thunder() {
      if (this.index < 9) {
        return "0" + (this.index + 1);
      } else {
        return this.index + 1;
      }
    }
  }
};
</script>

<style lang='less'>
li.song.active {
  &:nth-child(2),
  &:nth-child(3),
  &:nth-child(4) {
    .left {
      .kkkk {
        color: #d0006e;
      }
    }
  }
}
li.song {
  &:last-child {
    margin-bottom: 64px;
  }
  display: flex;
  margin: 5px 0 5px 0;
  .left {
    color: #999;
  }
  dl.center {
    margin-left: 8px;
    padding-top: 5px;
    flex: 1;
    dt {
      color: #333;
      font-size: 17px;
      // margin-top: 5px;
      line-height: 25px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    dd {
      height: 20px;
      line-height: 20px;
      font-size: 12px;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      color: #888;
      i.sq {
        width: 10px;
        height: 10px;
        line-height: 10px;
        display: inline-block;
        text-align: center;
        background: url("../assets/logo2.png") no-repeat;
        background-size: 150px 117px;
      }
    }
  }
  .left,
  .right {
    width: 40px;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .right {
    .play {
      width: 22px;
      height: 22px;
      display: inline-block;
      background: url("../assets/logo2.png") no-repeat;
      background-size: 166px 97px;
      background-position: -24px 0;
    }
  }
}
</style>
